.quote-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 2rem 0;
  }
  
  .quote-container h1 {
    text-align: center;
    margin-bottom: 1.5rem;
  }
  
  .quote-container form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .quote-container label {
    display: flex;
    flex-direction: column;
    font-weight: bold;
  }
  
  .quote-container input,
  .quote-container textarea {
    padding: 0.5rem;
    font-size: 1rem;
    margin-top: 0.3rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .quote-container button {
    width: fit-content;
    align-self: flex-end;
  }
  