.about-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem 0;
  }
  
  .our-story,
  .contact-info,
  .contact-form {
    margin-bottom: 2rem;
  }
  
  .our-story h1,
  .contact-info h2,
  .contact-form h2 {
    margin-bottom: 0.5rem;
  }
  
  .contact-form form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .contact-form label {
    display: flex;
    flex-direction: column;
    font-weight: bold;
  }
  
  .contact-form input,
  .contact-form textarea {
    padding: 0.5rem;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .contact-form button {
    align-self: flex-start;
  }
  