.home-services-container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  /* Hero */
  .hero {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;
    padding: 2rem 0;
  }
  
  .hero-text {
    max-width: 600px;
  }
  
  .hero-text h1 {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
  }
  
  .hero-image img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
  }
  
  /* How It Works */
  .how-it-works {
    text-align: center;
    padding: 2rem 0;
  }
  
  .steps {
    display: flex;
    justify-content: space-around;
    gap: 1rem;
    flex-wrap: wrap;
  }
  
  .step {
    max-width: 250px;
  }
  
  /* Services Overview */
  .services-overview {
    padding: 2rem 0;
  }
  
  .services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1.5rem;
  }
  
  .service-card {
    padding: 1.5rem;
    border: 1px solid #ddd;
    border-radius: 8px;
    text-align: center;
  }
  