.reviews-container {
    max-width: 1000px;
    margin: 0 auto;
    padding: 2rem 0;
  }
  
  .reviews-container h1 {
    text-align: center;
    margin-bottom: 2rem;
  }
  
  .reviews-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
  }
  
  .review-card {
    padding: 1.5rem;
    border: 1px solid #ddd;
    border-radius: 8px;
    text-align: center;
  }
  
  .review-card .stars {
    font-size: 1.2rem;
    color: #f5a623;
    margin-bottom: 0.5rem;
  }
  
  .review-card .text {
    font-style: italic;
    margin-bottom: 0.5rem;
  }
  
  .review-card .name {
    font-weight: bold;
  }
  
  .bottom-cta {
    text-align: center;
    margin-top: 2rem;
  }
  